h1, h2, h3, h4, h6, ul, li, button{
    color: var(--font-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: none;
    font-style: normal;
    border: none;
    background-color: transparent;
    margin: 0;
    transition: 0.3s ease-in-out;
    list-style: none;
}

p{
    color: var(--font-color);
    letter-spacing: 1px;
    margin: 0;
    transition: 0.3s ease-in-out;
    font-family: 'Tusker Grotesk', sans-serif;

}

h1{
    font-size: 2em;
    margin-bottom:15px;
}


.scrolled{
    position: fixed;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid var(--font-color);
    width: 100%;
    backdrop-filter: blur(3px);
    height: auto;
    z-index: 1;
    top: 0px;
    padding: 15px 0px 10px 0px;
    transition: 0.4s ease-in-out;

    h1 {
        font-size: 16px;
        margin-bottom: 2px;
    }

    h4{
        font-size: 12px;
        border-bottom: 0px;
        padding: 0px;
    }
}  

h3{
    margin-top: 20px;
}

h4{
    font-size: 1em;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--font-color);
    color: var(--tertiary-color);
}

h6{
    margin: 0px 0px 10px 0px;
}

ul{
    padding-left: 0px;
}

.intro-h4{
    border-bottom: 0px;
}

button{
    color: var(--font-color);
    background: transparent;
    border: none;
    font-weight: 600;
}

.button{
    margin: 10px;
    justify-self: left;
    margin-left: 0px;
    padding-left: 0px;
}
.buttonCC,.buttonC, .button{
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
    ul{
        margin-right: 0;
    }
}

.buttonC{
    margin-right: 40px;
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: #F4F6F6; }
.boxB {background-color: #1A1E23; }

.homepage-movement{
    position: relative;
    display: grid;
    justify-items: center;
    h3{
        justify-self: center;
        margin: 20px;
    }
    h5{
        font-size: 13px;
    }
}

.icon-button-switch{
    position: relative;
    font-size: 16px;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
}

.icon-button-switch-container{
    position: relative;
    display: grid;
    margin: 20px;
    padding: 20px;
    width: 400px;
    border-bottom: 1px solid var(--font-color);
    border-top: 1px solid var(--font-color);
}

.icon-container-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    letter-spacing: 2px;
    
}

.icon-container{
    position: relative;
    display: flex;
    align-items: center;
    justify-items: space-around;
    background-color: var(--secondary-color);
    flex-basis: calc(20%); /* 20% for 5 items per line, minus margins */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add shadow */
    border-radius: 10px;
    padding: 0px 10px 0 10px;
    margin: 20px;
    padding: 10px;
    min-width: 250px;
    cursor:pointer;
    overflow: hidden;
    &:hover{
        transform: translateY(-5px);
        background-color: var(--secondary-color);
        .home-noPro{transform: translateX(-50%);}
        .home-htPro{transform: translateX(-33%);} 
        .home-csPro{transform: translateX(-28%);}
        .home-jsPro{transform: translateX(-43%);}
        .home-jaPro{transform: translateX(-47%);}
        .home-sqPro{transform: translateX(-25%);}
        .home-pyPro{transform: translateX(-47%);}
        .home-cPro {transform: translateX(-34%);}
        .home-saPro{transform: translateX(-28%);}
        .home-unPro{transform: translateX(-57%);}
        .home-conPro{transform: translateX(-14%);}
    }
    
    .home-conPro{transition: 0s;}
}

.icons{
    height: 50px;
    width: 50px;
    margin-right: 10px;
}

.home-noPro, .home-htPro, .home-csPro, .home-jsPro, .home-jaPro,.home-sqPro,.home-pyPro,.home-cPro,.home-saPro,.home-unPro, .home-conPro{
    position: absolute;
    background-color: var(--tertiary-color);
    height: 100%;
    width: 120%;
    transform: translateX(-105%);   
    transition: 0.3s ease-in-out;    
    z-index: -1; 
}

.page{
    position: relative;
    //height: 100vh; 
    width: 100%;
    //margin: 50px 0px 50px 0px;
}

.colourPanel{
    position: fixed;
    display:flex;
    bottom: 0;
    margin-bottom: 15px;
    backdrop-filter: blur(3px);
}

.order{
    position: relative;
    display: flex;
    //height: calc(100% - (3rem + 65px));
}

.links{
    margin-top: 30px;
    position: sticky;
    display:grid;
    justify-content: start;
    width: 120px;
    border-right: 1px solid var(--font-color);
    height: 185px;
    top: 50px;
    transition: 0.3s ease-in-out;

}

.links.scrolled{
    position: fixed;
    display: inline;
    top: 0px;
    right: 0px;
    height: 65px;
    margin: 0;
    width: auto;
    border: 0;
    backdrop-filter: blur(0px);
}

.page-display{
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin-left: 20px;
    width: calc(100% - 120px);
}

.display{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border: 3px solid black;
    height: 500px;
    width: 45vw;
    overflow: visible;
}

.projectDiv{
    position: relative;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.projectUL{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: top;
    justify-content: space-around;
    overflow: hidden;
    h2{
        margin-top: 20px;
    }
}

.projectULL {
    height: 500px;
    position: sticky;
    top: 80px;   
}

.blur-overflow-container{
position: relative;
display: flex;
height: 500px;
overflow: hidden;
width: 98%;//---------------------------------
justify-content: center;
align-items: center;
}

.projectList{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 550px;
    overflow-y: scroll;
    padding: 50px 30px 70px 0px;
    &::-webkit-scrollbar {
        display: none; 
    }
}

.blur-container-bottom, .blur-container-top{
    position: absolute;
    width: 100%;
    height: 40px;
    backdrop-filter: blur(4px);
}

.blur-container-bottom {bottom: 0;}

.blur-container-top {top: 0;}

.listTags{
    display: flex;
    align-items: baseline;
    
    h3{
        font-size: 4em;
        font-weight: 100;
    }
    h6{
        font-size: 0.5em;
    }
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
        h3{color:#a6adb4}

    }
    

}

#react-unity-webgl-canvas-2{
    width: 100vw;
    aspect-ratio: 16 / 9;
}

.titles {
    text-align: center;
    padding: 0; /* Add this line to override the default margin */
}

.site-video, .pitc-img{
    width: 700px;
}

.pitc-button{
    padding: 1px;
    :hover{
        cursor: pointer;
    }
}

.close-icon{
    position: fixed;
    top: 30px;
    right: 30px;
    height: 30px;
    width: 30px;
    
}

.infectionSim{
    height: 800px;
    width: 800px;
}

.showcase {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.9);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(4px);
  }

@media (max-width: 1620px) {

}

@media (max-width: 1490px) {
    .blur-overflow-container{
        justify-content: start;
        align-items: center;
        }

    .projectUL{
        margin-top: 50px;
    }

    .projectULL {
        height: 500px;
        position: relative;
    }

    .projectDiv{
        flex-direction: column;
    }
}

@media (max-width: 1295px) {
    .projectUL{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
    }

    .display, .site-video,.pitc-img{
        width: 80vw;
        aspect-ratio: 16 / 9;
        max-width: 700px;
        
    }

    #react-unity-webgl-canvas-2{
        width: 80vw;
        aspect-ratio: 2 / 1;
        max-width: 700px;
    }

}


@media (max-width: 600px){
    h1, h2, h3, h4, ul, li, button{
        white-space:inherit;
    }    

    .page-display{
        padding: 0px;
        margin-left: 0px;
        width: calc(100% - 120px);
    }

    .listTags{
        flex-direction: column;
    }

    .order{
        flex-direction: column;
        .links{
            position: relative;
            display:flex;
            height: auto;
            width: 100%;
            top: 0px;
            font-size: x-small;
            justify-content: space-around;
            border-bottom: 1px solid var(--font-color);
            border-right: 0px;
            margin-bottom: 20px;
            padding-bottom: 20px;
            
        }

    }

    

    .homepage-movement{
        justify-items: start;
        h3{
            justify-self: start;
        }
    }

    .titles{
        text-align:start;
        margin: 20px;
    }   
}

@media (max-width: 500px){
    .icon-button-switch-container{
        width: auto;
        min-width: 250px;
    }
}

@media (max-width: 440px){
    .icon-container-container{
        justify-content: start;
    }

    .menuButton{
        position: fixed;
        display:grid;
        top: 20px;
        right: 20px;
        padding: 10px;
        height: 50px;
        width: 50px;
        backdrop-filter: blur(4px);
        justify-content: center;
        align-content: center;
        border: 1px solid var(--font-color);
        border-radius: 100%;
    }
}